#sign-in-button{
    background-color: #1977f3;
    width: 200px;
    border: 0;
    border-radius: 5px;
    position: absolute;
    bottom: 10px;
    right: 0;
    left: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    color: white;
    font-size: 16px;
    font-weight: 800;
    transition: all 300ms ease-in-out;
}
#sign-in-button:hover{
    background-color: white;
    color: #1977f3;
}
#sign-in-button:focus{
    border: 0;
}
h1{
    text-align: center;
    font-family: 'Titillium Web', sans-serif;
    color: #1977f3;
}
body{
    overflow-x: hidden;
}
.nav-bar{
    overflow-x: hidden;
    z-index: 999999;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    box-shadow: 0px 0px 3px 0px black;
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    right: 0;
    left: 0;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 2px;
}
img{
    height: 50px;
}
.input-and-image{
    display: flex;
    align-items: center;
}
/* .input-and-image *{
    margin: 5px;
} */
.input-and-image div{
    display: flex;
    height: 40px;
    width: 200px;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    background-color: #e6e6e6;
    border-radius: 50px;
    transition: all 300ms ease-in;
    /* padding: 5px; */
}
.input-and-image div input{
    background-color: transparent;
    border: 0;
    width: 60%;
    margin-left: 10px;
    
}
.input-and-image div input:focus{
    border: 0;
    outline: none;
}
.input-and-image div input:focus+.input-and-image div{
    width: 230px;
}
.buttons button {
    background-color: transparent;
    border: 0;
    outline: 0;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    display: inline-block;
    
}
.buttons button:hover{
    background-color:#e6e6e6;
}


.buttons *{
    position: relative;
}
.buttons *:focus:after{
    color: blue;
    background-color: blue;
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    height: 1px;
    padding: 1px;
}
.buttons *:focus{
    color: blue;
}


.user-div{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    padding: 5px;
    transition: all 300ms ease-in;
}

.user-div button:hover{
    background-color: #e6e6e6;
}
.user-div button{
    /* margin-left: auto;
    margin-right: 20px;
     */
     padding: 5px;
     border-radius: 6px;
     margin: 10px;
    outline: none;
    border: 0;
    background-color: transparent;

}
@media only screen and (max-width: 1000px){
	.buttons{
        overflow-x: hidden;
        background-color: white;
        position: absolute;
        display: flex;
        bottom: 0;
        left: 0;
        right: 0;
        align-items: center;
        justify-content:center;
        right: 0;
        /* background-color: red; */
    }
}

@media only screen and (max-width: 680px){
	.user-div{
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        top: 0;
        background-color: white;
        transform: translate(300px);
        align-items: center;
        justify-content: center;
    }
   
}

#open-button{
    background-color: transparent;
    border: 0;
    outline: 0;
    display: none;
    
}
#close-button{
    background-color: transparent;
    border: 0;
    outline: 0;
    display: none;
}
@media only screen and (max-width: 680px){
    #open-button{
